  50% {
    transform: translate3d(0, 0, 0);
    width: 70%; }
  51% {
    transform: translate3d(0, 0, 0);
    width: 100%; }
  100% {
    width: 100%;
    transform: translate3d(0, 0, 0) rotate(45deg); } }

@keyframes hamburger-top-back {
  0% {
    transform: translate3d(0, -5px, 0); }
  50% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(0, 0, 0) rotate(-45deg); } }

@keyframes hamburger-mid-back {
  0% {
    transform: translate3d(0, 5px, 0);
    width: 70%; }
  50% {
    transform: translate3d(0, 0, 0);
    width: 70%; }
  51% {
    transform: translate3d(0, 0, 0);
    width: 100%; }
  100% {
    width: 100%;
    transform: translate3d(0, 0, 0) rotate(45deg); } }

